.glassmorphism-container {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	background: linear-gradient(to right, #d4d4d4, #f0f0f0);
}

.glassmorphism-card {
	position: fixed;
	background: rgba(255, 255, 255, 0.1);
	border: 2px solid #f0f0f0;
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h1 {
	color: #333;
}

p {
	color: #666;
}